/* Mobile & Tablet */
@media (max-width : 1023px) {
    .hide-on-mobile-tablet {
        display: none;
    }
    .header {
        position: fixed;
        z-index: 10;
        top: 0;
        left: 0;
        right: 0;
    }
    .header__cart-list {
        position: fixed;
        top: var(--header-height);
        right: 0px;
        width: 100%;
        padding: 0 calc((100% - 644px) /2);
    }
    .header__cart-list::after {
        display: none;
    }
    .header__cart-item-price-wrap {
        display: flex;
    }
    .header__cart-item-remove {
        display: none;
    }
    .header__cart-item-remove-icon {
        display: block;
        padding:0 20px;
        top: -3px;
        color: var(--text-color);
        font-weight: 700;
    }
    .header__sort-bar {
        display: flex;
    }
    .app_container {
        margin-top: calc((var(--header-height)) + (var(--header-sort-bar-height)));
    }
    .app__content {
        padding-top: 8px;
    }
    .mobile-category {
        display: block;
    }
}

/* Tablet */
@media (min-width : 740px ) and (max-width : 1023px) {
    :root {
        --header-height : 68px;
        --navbar-height : 0px;
    }
    .hide-on-tablet {
        display: none;
    }
    .header__cart{
        width: 80px;
        text-align: right;
    }
    .mt-12-t{
        margin-top: 12px;
    }
}

/* Mobile */
@media (max-width : 739px) {
    :root {
        --header-height : 52px;
        --navbar-height : 0px;
    }
    .hide-on-mobile {
        display: none;
    }
    .nav__bars-btn {
        display: block;
        /* margin-left: 30px; */
        margin-top: 10px;
        margin-bottom: 15px;
    }
    .header__cart {
        margin: 0;
        width: 50px;
        font-size: 3.2rem;
    }
    .header-with-search {
        position: relative;
        justify-content: space-between;
    }
    .header__logo {
        width: unset;
    }
    .header__logo-img {
        width: 110px;
    }
    .header__mobile-search {
        display: block;
    }
    .header__mobile-search-icon {
        /* margin-left: -20px; */
        position: absolute;
        margin-top: -12px;
        margin-bottom: 10px;
    }
    .header__search {
        position: fixed;
        top: var(--header-height);
        left: 0;
        right: 0;
        height: 46px;
        box-shadow: 0 1px 2px rgba(0 , 0 , 0 , 0.05);
        z-index: 1;
        /* padding: 0 16px; */
        animation: mobileSearchSideIn linear 0.3s;
        display: none;
    }
    #mobile-search-checkbox {
        opacity: 0;
        margin-left: -46px;
    }
    .header__search-history {
        position: fixed;
        width: 100%;
        left: 0;
        top: calc((var(--header-height)) + var(--header-sort-bar-height));
        box-shadow: 0 1px 2px rgba(0 , 0 , 0 , 0.05);
        
    }
    .pagination-item {
        margin: 0;
    }
    .pagination-item__link {
        --height : 36px;
        font-size: 1.6rem;
        min-width: 34px;
    }
    .app__content {
        margin: 0 4px;
    }
    .header__sort-bar:first-child {
        border-bottom: 2px solid var(--primary-color);
    }
    .footer__content {
        padding: 16px;
    }
    @keyframes mobileSearchSideIn {
        from {
            opacity: 0;
            top: calc(var(--header-height) / 2);
        }
        to {
            opacity: 1;
            top: var(--header-height);
        }
    }
    .footer__heading {
        text-align: center;
    }
    .footer__download,
    .footer-item__link {
        justify-content: center;
    }
    .footer__download-apps {
        flex : unset;
    }
    .mt-12-m{
        margin-top: 12px;
    }
}